<template>
  <v-container fluid>
    <v-card flat max-width="1440" class="transparent mx-auto mb-10 pb-12">
      <v-data-table
        :headers="headers"
        :options.sync="options"
        :items="items"
        item-key="_id"
        class="elevation-0 pa-8"
        :single-expand="true"
        :expanded.sync="expanded"
        calculate-widths
        multi-sort
        show-expand
        dense
      >
        <template v-slot:top>
          <v-toolbar flat>
            <v-toolbar-title>
              <h5>
                {{ expandedItemName }}
              </h5>
            </v-toolbar-title>
            <v-spacer></v-spacer>
            <v-btn
              color="#09b"
              text
              class="mb-2"
              @click="createNewBuilding = true"
            >
              New Building
            </v-btn>

            <NewBuildingListItem
              :opened.sync="createNewBuilding"
              :save.sync="updateListOfBuildings"
            />

          </v-toolbar>
        </template>
        <template v-slot:item.connect="{ item }">
          <v-icon class="mr-4" :color="getColor(item.status)">{{ getIcon(item.status) }}</v-icon>
        </template>
        <template v-slot:item.actions="{ item }">
          <!-- <v-icon class="mr-4" :color="getColor(item.status)">{{ getIcon(item.status) }}</v-icon> -->
          <!-- <v-icon small class="mr-2" @click="editItem(item)"> mdi-pencil </v-icon> -->
          <v-icon small class="mr-2" color="#080" @click="_saveBuilding"> $save </v-icon>
          <v-icon small color="#900" @click="deleteItem(item)"> $delete </v-icon>
        </template>

        <template v-slot:body.append>
          <tr style="background: #eaeaea; font-size: 12px!important">
            <td width="160">
              <v-text-field
                v-model="state"
                label="State"
                hide-details
                append-icon="mdi-magnify"
              ></v-text-field>
            </td>
            <td width="180">
              <v-text-field
                v-model="postcode"
                label="Postal Code"
                hide-details
                append-icon="mdi-magnify"
              ></v-text-field>
            </td>
            <td width="280">
              <v-text-field
                v-model="city"
                label="City"
                hide-details
                append-icon="mdi-magnify"
              ></v-text-field>
            </td>
            <td>
              <v-text-field
                v-model="street"
                label="Street Name"
                hide-details
                append-icon="mdi-magnify"
              ></v-text-field>
            </td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
          </tr>
        </template>
        <template v-slot:expanded-item="{ headers, item }">
          <td :colspan="headers.length" class="pa-10">
            <BuildingListItem
              :editedItem.sync="item"
              :buildingId.sync="buildingId"
            />
          </td>
        </template>
      </v-data-table>
      <Legend />
      <v-btn text @click="test">Goto last page</v-btn>
    </v-card>
    <PopupDelete :opened.sync="dialogDelete" :delete.sync="deleteConfirmed" />
  </v-container>
</template>

<script>

export default {

  name: 'ListOfBuildings',

  components: {
    PopupDelete: () => import('@/components/inputs/PopupDelete.vue'),
    NewBuildingListItem: () => import('@/components/building/NewBuildingListItem.vue'),
    BuildingListItem: () => import('@/components/building/BuildingListItem.vue'),
    Legend: () => import('@/components/Legend.vue')
  },

  props: ['selectedBuildingId'],

  data: () => ({
    createNewBuilding: false,
    updateListOfBuildings: false,
    dialogDelete: false,
    deleteConfirmed: false,
    expanded: [],
    expandedItemName: null,
    items: localStorage.getAllBuildings()
      .map(item => Object.assign(item.addressComponents, {
        _id: item._id,
        buildingName: item.buildingName,
        status: item.status,
        isActive: false
      })),
    options: {
      page: 1,
      itemsPerPage: 10
    },
    postcode: '',
    state: '',
    city: '',
    street: '',
    editedIndex: -1,
    editedItem: {}
  }),

  computed: {
    buildingId: {
      get () {
        return this.selectedBuildingId
      },
      set (val) {
        this._getBuildingDetails(val)
          .then(() => { this.$emit('update:selectedBuildingId', val) })
      }
    },
    headers () {
      return [
        {
          text: 'State',
          align: 'start',
          sortable: true,
          value: 'state',
          filter: value => {
            if (!this.state) return true
            return value.toLowerCase().indexOf(this.state.toLowerCase()) !== -1
          }
        },
        {
          text: 'Postal Code',
          value: 'postCode',
          filter: value => {
            if (!this.postcode) return true
            return value === this.postcode
          }
        },
        {
          text: 'City',
          value: 'city',
          filter: value => {
            if (!this.city) return true
            return value.toLowerCase().indexOf(this.city.toLowerCase()) !== -1
          }
        },
        {
          text: 'Street Name',
          value: 'street',
          filter: value => {
            if (!this.street) return true
            return value.toLowerCase().indexOf(this.street.toLowerCase()) !== -1
          }
        },
        {
          text: 'Street Number',
          value: 'number'
        },
        {
          text: 'Status',
          value: 'connect',
          sortable: false
        },
        {
          text: 'Actions',
          value: 'actions',
          sortable: false
        },
        { text: '', value: 'data-table-expand' }
      ]
    }
  },

  watch: {
    createNewBuilding (val) {
      if (val) {
        this.expanded = []
        this.buildingId = null
        localStorage.setItemByName('selectedBuilding', null)
        this.dialog = true
      }
    },
    updateListOfBuildings (val) {
      if (!val) return
      const building = localStorage.getItemByName('selectedBuilding')
      this.items.push(Object.assign(building.addressComponents, {
        _id: building._id,
        buildingName: building.buildingName,
        status: building.status,
        isActive: true
      }))

      this.expanded = [this.items[this.items.length - 1]]
      Object.assign(this.options, { page: Math.floor(this.items.length + 1 / this.options.itemsPerPage) })
      this.updateListOfBuildings = false
    },
    expanded (val) {
      if (!val[0]) {
        this.items.forEach(item => Object.assign(item, { isActive: false }))
        this.expandedItemName = ''
        this.$emit('update:selectedBuildingId', null)
        return
      }
      const index = this.items.indexOf(val[0])
      this.items[index].isActive = true
      this.expandedItemName = `${val[0].number} ${val[0].street}, ${val[0].city} ${val[0].state} ${val[0].postCode}`
    },
    deleteConfirmed (val) {
      val && this.deleteBuilding()
    }
  },

  methods: {
    test () {
      console.log(this.options.itemsPerPage, Math.floor(this.items.length / this.options.itemsPerPage))
      Object.assign(this.options, { page: Math.floor(this.items.length + 1 / this.options.itemsPerPage) })
    },
    getIcon (status) {
      const index = this.$buildingTypes.indexOf(status)
      if (index === -1) {
        console.warn('Invalid Building Status: ', status)
        return this.$typeIcons[this.$typeIcons.length - 1][0]
      }
      return this.$typeIcons[index]
    },
    getColor (status) {
      const index = this.$buildingTypes.indexOf(status)
      if (index === -1) {
        console.warn('Invalid Building Status: ', status)
        return this.$typeColors[this.$typeColors.length - 1][0]
      }
      return this.$typeColors[index]
    },

    deleteItem (item) {
      this.editedIndex = this.items.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.dialogDelete = true
    },

    async deleteBuilding () {
      await this._deleteBuilding(this.items[this.editedIndex]._id)
      this.items.splice(this.editedIndex, 1)
      this.$nextTick(() => {
        this.expanded = []
        this.editedItem = null
        this.editedIndex = -1
      })
      this.dialogDelete = false
      this.deleteConfirmed = false
    }
  }
}
</script>

<style scoped>
.active {
  background: #900;
}
.passive {
  background: #fafafa;
}
</style>
